export default [
  {
    id: "intro-1",
    message: "Hello, Jennifer !",
    trigger: "intro-2",
  },
  {
    id: "intro-2",
    message: "I'm an AI chatbot built by counselors.",
    trigger: "intro-3",
  },
  {
    id: "intro-3",
    message: "How can I help you today?",
    trigger: "main-options",
  },
  {
    id: "main-options",
    options: [
      { value: 1, label: "Improve mental wellbeing", trigger: "wellbeing" },
      { value: 2, label: "Improve my daily lifestyle", trigger: "lifestyle" },
      {
        value: 3,
        label: "Assistance with an administrative or bureaucratic issue",
        trigger: "assistance",
      },
      { value: 4, label: "Get support now", trigger: "support" },
      {
        value: 5,
        label: "Learn more about my benefits and options",
        trigger: "",
      },
      { value: 6, label: "I'm not sure", trigger: "not-sure" },
    ],
  },
  //wellbeing
  {
    id: "wellbeing",
    message: "Do you have any particular goals?",
    trigger: "wellbeing-options",
  },
  {
    id: "wellbeing-options",
    options: [
      { value: 1, label: "Build Resilience", trigger: "global-option-2" },
      {
        value: 2,
        label: "Improve relationships",
        trigger: "global-option-1",
      },
      {
        value: 3,
        label: "Feel less lonely or isolated",
        trigger: "global-option-1",
      },
      {
        value: 4,
        label: "Feel happier in general",
        trigger: "global-option-1",
      },
      {
        value: 5,
        label: "Feel less worried or anxious",
        trigger: "global-option-1",
      },
      {
        value: 6,
        label: "I'm not sure or none of these",
        trigger: "global-option-1",
      },
    ],
  },
  {
    id: "global-option-1",
    message:
      "To get you the appropriate resources, I'll be asking you some questions around your mood and anxiety. This will result in more customized solutions.",
    trigger: "global-option-1a",
  },
  {
    id: "global-option-1a",
    message: "It will take you less than 5 minutes ",
    trigger: "global-option-1b",
  },
  {
    id: "global-option-1b",
    message: "Let's start",
    trigger: "global-option-1-options",
  },
  {
    id: "global-option-1-options",
    options: [
      {
        value: 1,
        label: "Okay",
        trigger: ({ steps }) => {
          const all = Object.keys(steps);
          const final = all.filter((element) => element == "assistance");

          return final.length == 0 ? "eval-1-1" : "eval-2-1";
        },
      },
    ],
  },
  {
    id: "global-option-2",
    message:
      "To get you the appropriate resources, I'll be asking you some questions around your mood and anxiety. This will result in more customized solutions.",
    trigger: "global-option-2a",
  },
  {
    id: "global-option-2a",
    message: "It will take you less than 5 minutes ",
    trigger: "global-option-2b",
  },
  {
    id: "global-option-2b",
    message: "Do you want to start?",
    trigger: "global-option-2-options",
  },
  {
    id: "global-option-2-options",
    options: [
      {
        value: 1,
        label: "Maybe later",
        trigger: ({ steps }) => {
          const all = Object.keys(steps);
          const final = all.filter((element) => element == "wellbeing");

          return final.length == 0 ? "resources" : "later-chat-1";
        },
      },
      {
        value: 2,
        label: "Yes, let's start",
        trigger: ({ steps }) => {
          const all = Object.keys(steps);
          const final = all.filter((element) => element == "wellbeing");

          return final.length == 0 ? "eval-2-1" : "eval-1-1";
        },
      },
    ],
  },

  {
    id: "later-chat-1",
    message: "That's alright, Jennie.",
    trigger: "later-chat-2",
  },
  {
    id: "later-chat-2",
    message:
      "Let me ask you what strategy would you like to use for building your resilience?",
    trigger: "strategy-options",
  },
  {
    id: "strategy-options",
    options: [
      {
        value: 1,
        label: "Calm yourself with mindfulness",
        trigger: "strategy-1",
      },
      {
        value: 2,
        label: "Talk through negative thoughts",
        trigger: "strategy-2",
      },
      { value: 3, label: "Build better habits", trigger: "strategy-3" },
    ],
  },
  {
    id: "strategy-1",
    message: "The Calm app is great for dealing with anxiety and sleep issues.",
    trigger: "strategy-1a",
  },
  {
    id: "strategy-1a",
    message:
      "They have great mindfulness audios and sleep stories, many narrated by celebrities.\nTheir sleep sounds are particularly popular with your colleagues.",
    trigger: "strategy-1b",
  },
  {
    id: "strategy-1b",
    message: "Do you want to try the calm app? ",
    trigger: "access-options",
  },
  {
    id: "strategy-2",
    message:
      "AbleTo's  committed to delivering the highest quality mental health support from virtual therapy and coaching to on-demand self care.",
    trigger: "strategy-2a",
  },
  {
    id: "strategy-2a",
    message:
      "Their virtual therapy and coaching programs can help you reduce feelings of stress, anxiety, and depression, while on-demand self care gives you the tools you need to build resilience and cope with everyday challenges. ",
    trigger: "strategy-2b",
  },
  {
    id: "strategy-2b",
    message: "Do you want to try AbleTo?",
    trigger: "access-options",
  },
  {
    id: "strategy-3",
    message:
      "RethinkCare offers virtual consulattions with behavior experts that allow you to address issues in a safe place.",
    trigger: "strategy-3a",
  },
  {
    id: "strategy-3a",
    message:
      "It also has the resources to cope with issues like developmental and behavioral problems faced when raising toddlers and adolescents.",
    trigger: "strategy-3b",
  },
  {
    id: "strategy-3b",
    message: "Do you want to try RethinkCare?",
    trigger: "access-options",
  },
  {
    id: "access-options",
    options: [
      { value: 1, label: "Maybe later", trigger: "bye-chat-1" },
      { value: 2, label: "Yes, get me access", trigger: "popout" },
    ],
  },
  {
    id: "bye-chat-1",
    message: "That's alright, Jennie.",
    trigger: "bye-chat-1a",
  },
  {
    id: "bye-chat-1a",
    message:
      "You can always checkout 'Your Benefits & Resources' section on the homepage for immediate access to resources. ",
    trigger: "bye-chat-1b",
  },
  {
    id: "bye-chat-1b",
    message: "Bye ",
    trigger: "bye-options",
  },
  {
    id: "bye-options",
    options: [{ value: 1, label: "Bye ", end: true }],
  },
  //lifestyle
  {
    id: "lifestyle",
    message: "What are you looking to improve?",
    trigger: "lifetsyle-options",
  },
  {
    id: "lifetsyle-options",
    options: [
      { value: 1, label: "Drinking/drug use", trigger: "global-option-1" },
      { value: 2, label: "Sleep better", trigger: "global-option-1" },
      {
        value: 3,
        label: "Have more energy/focus",
        trigger: "global-option-1",
      },
      { value: 4, label: "Work life balance", trigger: "global-option-1" },
      {
        value: 5,
        label: "I'm not sure or none of these",
        trigger: "global-option-1",
      },
    ],
  },
  //assistance
  {
    id: "assistance",
    message: "What type of support do you need?",
    trigger: "assistance-options",
  },
  {
    id: "assistance-options",
    options: [
      { value: 1, label: "Legal Guidance", trigger: "global-option-2" },
      { value: 2, label: "Financial Coaching", trigger: "global-option-2" },
      { value: 3, label: "Parenting", trigger: "global-option-2" },
      {
        value: 4,
        label: "Management Consultation or Refferal",
        trigger: "global-option-2",
      },
      {
        value: 5,
        label: "I'm not sure or none of these",
        trigger: "global-option-1",
      },
    ],
  },
  //support
  {
    id: "support",
    message: "What type of support do you need?",
    trigger: "support-options",
  },
  {
    id: "support-options",
    options: [
      { value: 1, label: "Provider Search", trigger: "global-option-2" },
      { value: 2, label: "Digital Auth", trigger: "global-option-2" },
      { value: 3, label: "LAWW Deeplink", trigger: "global-option-2" },
      { value: 4, label: "EAP Phone Number", trigger: "global-option-2" },
      {
        value: 5,
        label: "Behavioral Health Resources",
        trigger: "popout",
      },
    ],
  },
  //notsure
  {
    id: "not-sure",
    options: [
      {
        value: 1,
        label: "Would learning more about your benefits be beneficial?",
        trigger: "popout",
      },
      {
        value: 2,
        label: "I want to feel better, but i'm not sure where to start.",
        trigger: "global-option-1",
      },
      {
        value: 3,
        label: " Could you please provide me with some suggestions?",
        trigger: "global-option-1",
      },
    ],
  },
  {
    id: "popout",
    message: "POPOUT PLACEHOLDER",
    trigger: "",
  },
  {
    id: "resources",
    message: "RESOURCES PLACEHOLDER",
    trigger: "",
  },
];
