import React from "react";
import ChatBot from "react-simple-chatbot";
import PageCenter from "atom/PageCenter";
import { Heading } from "atom/Typography";
import { LabelwithEmphasis } from "atom/Typography";
import data from "./data";
import eval2 from "./eval2";
import eval1 from "./eval1";
import style from "./style.scss";
import cn from "classnames";

const rootStyle = {
  boxSizing: "border-box",
  backgroundColor: "var(--palette-white)",
  width: "100%",
  height: "100%",
  borderRadius: "40px 0 40px 0",
  padding: 24,
};

const bubbleStyle = {
  borderRadius: 12,
  color: "var(--palette-dark-blue)",
  padding: 12,
  boxShadow: "none",
};

const bubbleOptionStyle = {
  backgroundColor: "transparent",
  border: "1px solid var(--palette-dark-blue)",
  color: "var(--palette-dark-blue)",
  cursor: "pointer",
  padding: "8px 12px",
  marginBottom: 8,
};

const Chatbot = ({}) => (
  <PageCenter rootClass={cn(style.root)} contentClass={cn(style.content)}>
    <ChatBot
      hideHeader={true}
      hideUserAvatar={true}
      userDelay={100}
      botDelay={1500}
      avatarStyle={{ boxShadow: "none", padding: 0 }}
      bubbleStyle={bubbleStyle}
      bubbleOptionStyle={bubbleOptionStyle}
      footerStyle={{ display: "none" }}
      style={rootStyle}
      steps={[...data, ...eval1, ...eval2]}
    />
  </PageCenter>
);

export default Chatbot;
